import React,{Component} from 'react';
import { Link } from 'react-router-dom';
import Button from '../../../components/Button/index'
import {
    RecentOrderWrapper,
    OrderTitle,
    OrderItem
}from '../style'
class  RecentOrder extends  Component{
   
    render(){
        const { userrecentOrder }=this.props;
        return(
            <RecentOrderWrapper>
                <OrderTitle class="goodsTitle"> 最近订单</OrderTitle>
                <div class="col">
                    <ul class="lg-list">
                        {
                            userrecentOrder.map((item)=>{
                                return (
                                    <OrderItem class="lg-item" key={item.id}>
                                       
                                        <img src={item.image} alt="订单图片"/>
                                        {console.log("最近订单"+JSON.stringify(item))}
                                        <p class="detail">送达：{item.detailAddress }&nbsp;&nbsp;&nbsp;联系方式:{userrecentOrder.userid }</p>
                                        <Link to={{
                                                pathname:"/book/commentBook",
                                                state:JSON.stringify(item)
                                            }}>
                                            评价
                                        </Link>
                                    </OrderItem>
                            )
                            })
                        }
                       
                    </ul> 
                </div>
            </RecentOrderWrapper>
        )
    }
}

export default RecentOrder;