import {  HashRouter, Route , Switch } from 'react-router-dom';
import React,{ Component ,Fragment } from 'react';
import ManagerSearchForm from './components/ManagerSearchForm'
import ManagerBook from './managerBook';
import ManagerUser from './managerUser';
import ManagerOrder from './managerOrder';
class ManagePage extends Component{
  
    render(){
        return(
            <Fragment>
               <HashRouter>
                    <Switch>
                        <Route path="/manager/managerBook" exact component={ManagerBook}></Route>
                        <Route path="/manager/managerUser"  exact component={ManagerUser}></Route>
                        <Route path="/manager/ManagerOrder" exact component={ManagerOrder}></Route>
                
                    </Switch>
                </HashRouter>
            </Fragment>
        )
    }
}
export default ManagePage;